<template>
<!-- narrow是v-wide自定义指令的绑定值，通过binding.value获得 -->
<!-- background是v-wide自定义指令传的参数，通过binding.arg获得 -->
  <div v-wide:background="'narrow'" id="show-blogs">
   <h1>博客总览</h1>
   <input type="text" v-model="search" placeholder="搜索">
       <div  :key="index" v-for="(blog,index) in filteredBlogs" class="single-blog">
           <!-- | to-uppercase是过滤器 -->
             <router-link :to="'/blog/'+blog.id"><h2 v-color>{{blog.title |toUppercase}}</h2> </router-link>
               <!-- v-color自定义指令 -->
               <article>{{blog.content | snippet}}</article>
       </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'show-blogs',
  data() {
      return {
          blogs:[],
          search:''
      }
  },
  created() {
    //   this.$http.get("https://my-blog-1ab7b.firebaseio.com/post.json")
      axios.get("https://my-blog-1ab7b.firebaseio.com/post.json")
      .then(data=>{
           return data.data
      })
      .then(data=>{
          var blogArray=[];
          for (let k in data){
             // console.log(k);
              //这个k是数据库自动生成的一个数据，在这用来当成唯一标识
            //  console.log(data[k]);这是一个对象，里面是添加的内容
              data[k].id=k;
              //添加一个id属性，用来表示每一个singleblog
              blogArray.push(data[k])
          }
          this.blogs=blogArray
      })
  },
//   computed属性在生成实例时执行一次
//之后每当涉及的数据变化时都会执行
  computed: {
      filteredBlogs:function(){
          //filter()方法，对数组中的每个元素都筛选，返回一个新的数组
          //es6的新数组方法
          //match（）字符串匹配函数
          return this.blogs.filter(blog=>{
              return blog.title.match(this.search)
          })
      }
  },
  filters:{
      //特殊的形式，加‘’
      //局部过滤器有两种形式，驼峰的不用加引号，短横线的要加
      toUppercase: function(value) {
    return value.toUpperCase();
        },
        'snippet': function(value) {
    return value.slice(0, 200) + '...';
}
    },
    //局部自定义指令
    directives:{
        'color': {
            bind(el, binding, vnode) {
                el.style.color = '#' + Math.random().toString(16).slice(2, 5);
                }
        },
        'wide': {
    bind: function(el, binding, vnode) {
        //修改最大宽度
        if (binding.value == 'wide') {
            el.style.maxWidth = "1260px";
        } else if (binding.value == 'narrow') {
            el.style.maxWidth = "900px";
        }
        //修改背景颜色
        if (binding.arg == 'background') {
            // el.style.background = "skyblue",
                el.style.padding = '20px'
        }
    
        }
}

    }    
}
</script>

<style scoped>


  #show-blogs{
      max-width: 800px;
      margin: 0 auto;
      background: url('../assets/timg.jpg');
  }
  .single-blog{
      padding:0 20px 20px;
      margin: 20px 0;
      box-sizing: border-box;
      background: #eee;
      border: 1px dotted #aaa;
  }
  #show-blogs a{
      text-decoration: none;
  }
  input[type="text"]{
   padding: 8px;
   width: 100%;
   box-sizing: border-box;
  }
  
  
</style>



