<template>
    <div id="single-blog">
        <h1>{{blog.title}}</h1>
        <hr>
        <article>{{blog.content}}</article>
        <p>作者：{{blog.author}}</p>
        <ul>
            <span>类别：</span>
            <li :key="index" v-for="(category,index) in blog.categories">
               {{category}}
            </li>
        </ul>
        <button @click="deleteSingleBlog()">删除</button>
        <router-link :to="'/edit/'+id">编辑</router-link>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    name:'single-blog',
    data() {
        return {
            id:this.$route.params.id,
            blog:{}
        }
    },
    created() {
        // this.$http.get('https://my-blog-1ab7b.firebaseio.com/post/'+this.id+'.json')
       axios.get('/post/'+this.id+'.json')
        .then(data=>{
            this.blog=data.data
        })   
    },methods: {
       deleteSingleBlog(){
        //    this.$http.delete('https://my-blog-1ab7b.firebaseio.com/post/'+this.id+'.json')
        axios.delete('/post/'+this.id+'.json')
             .then(data=>{
                 this.$router.push({path:'/'})
             })
       }
    },
    
}
</script>
<style scoped>
#single-blog{
    max-width: 960px;
    margin: 0 auto;
    padding: 20px;
    border: 1px dotted #aaa;
}
button,a{
    color: white;
    background: darkblue;
    display: inline-block;
    padding: 5px 10px;
    /* border: 1px solid; */
    border-radius: 10px;
    text-decoration: none;
}
ul{
    list-style: none;
    margin: 0;
    padding: 0;
}
li{
    display: inline-block;
    margin:0 20px 15px 0;
}
span,li,p{
    font-size: 13px;
    color: grey;
}

</style>


