               <router-link to='/add' exact>写博客</router-link>
           </li>
       </ul> 
    </div>
</template>
    



<script>
export default {
    name:'blog-header',
}
</script>

<style  scoped>
ul{
  list-style-type: none;
  text-align: center;
  margin: 0 ;
}
li{
  display: inline-block;
  margin: 0 10px;
}
a{
text-decoration: none;
padding: 12px;
color:blue
}
.router-link-active{
    background: rgba(255, 255, 255, 0.8);
border-radius: 4px;
}
div{
  background: url('../assets/timg.jpg') center;
  padding: 30px 0;
  margin-bottom: 40px;
}
</style>


