<template>
  <div id="add-blog">
   <h2>添加博客 </h2>
   <!-- 添加博客成功后，表单不展示 -->
   <form v-if="!submitted" >
       <label for="">博客标题</label>
       <input type="text" v-model="blog.title" required>

       <label for="">博客内容</label>
       <textarea name="" id="" cols="30" rows="10" v-model="blog.content"></textarea>
      
       <div id="checkboxes">
           <span>分类：</span>
           <label for="">HTML</label>
           <input type="checkbox" value="HTML" v-model="blog.categories">
           <label for="">CSS</label>
           <input type="checkbox" value="CSS" v-model="blog.categories">
           <label for="">JavaScript</label>
           <input type="checkbox" value="Javascript" v-model="blog.categories">
           <label for="">Node.js</label>
           <input type="checkbox" value="Node.js" v-model="blog.categories">
           <label for="">Ajax</label>
           <input type="checkbox" value="Ajax" v-model="blog.categories">
           <label for="">Vue.js</label>
           <input type="checkbox" value="Vue.js" v-model="blog.categories">
       </div>

       <div id="author">
           <label for="">作者：</label>
           <input type="text"  v-model="blog.author">
       </div>

       <button v-on:click.prevent="post">添加博客</button>
   </form>
   <div v-if="submitted">您的博客添加成功！</div>
   <hr>
   <div id="preview">
       <h3>博客总览</h3>
       <p>博客标题：{{blog.title}}</p>
       <p>博客内容：</p>
       <p>{{blog.content}}</p>
       <p>博客分类：</p>
       <ul>
           <li :key="item" v-for="item in blog.categories">{{item}}</li>
       </ul>
         <p>作者：{{blog.author}}</p>
   </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'add-blog',
  data () {
    return {
      blog:{
          title:'',
          content:'',
          categories:[],
          author:''
      },
      submitted:false
    }
  },
  methods: {
      post:function(){
        //   that=this;
          //把数据传送到我的firebase中的my-blog项目，取名post.json,第二项参数是要传递的数据
        //   this.$http.post("https://my-blog-1ab7b.firebaseio.com/post.json",this.blog)
           axios.post("/post.json",this.blog)
          .then((data)=>{
                console.log(data);
                this.submitted=true;
          })
      }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add-blog *{
box-sizing: border-box;
}
#add-blog{
    margin: 20px auto;
    max-width: 600px;
    padding: 20px;
}
label{
    display: block;
    margin: 20px 0 10px;
}
input[type="text"],textarea{
display: block;
width: 100%;
padding: 8px;
}
textarea{
    height: 200px;
}
#checkboxes{
    display: inline-block;
    margin-top: 0;
    font-size: 13px;

}

#checkboxes label{
    display: inline;
}
#checkboxes input{
    display: inline-block;
    margin-right: 15px;
}
button{
    display: block;
    margin: 20px 0;
    background: sienna ;
    color: #ffff;
    border: 0;
    padding: 14px;
    border-radius: 4px;
    font-size: 18px;
    cursor: pointer;
    /* cursor是光标 */
}
#preview{
    padding: 10px 20px;
    border: 1px dotted #ccc;
    margin: 30px 0;
}
h3{
    margin-top: 10px;
}
</style>



